<template lang='pug'>
  
  .create-task
    input(type='text' placeholder='Заголовок задачи' v-model.trim='task.title').create-task__name
    textarea(placeholder='Описание задачи (необязательно)' v-model.trim='task.desc').create-task__desc
    .create-task__priority
      p.create-task__priority-title Приоритет задачи:
      .create-task__priority-list
        button(
          v-for='(btn, i) in priority.list' 
          :class='{"create-task__priority-button_selected": i == priority.selectedIndex}'
          @click='setPriority(i)'
        ).create-task__priority-button {{btn}}
    button(@click='createTask' :class='{"container__submit_disabled": loading}').container__submit.
      #[img(:src='require("@/assets/img/loading_white.svg")' v-show='loading')] {{ loading ? "Загрузка..." : "Добавить задачу" }}

</template>

<script>
  
  export default {
    data() {
      return {
        task: {
          title: '',
          desc: ''
        },
        priority: {
          list: ['Низкий', 'Средний', 'Высокий'],
          selectedIndex: 0
        },
        loading: false
      }
    },
    computed: {
      taskList() {
        return this.$store.state.tasks.list
      },
    },
    methods: {
      setPriority(i) {
        this.priority.selectedIndex = i
      },
      createTask() {
        if (this.loading) return this.$toast.error('Подождите, идет загрузка...')
        else if (!this.task.title) return this.$toast.error('Укажите заголовок задачи')
        else if (this.task.title.length >= 50 || this.task.desc.length >= 150) 
          return this.$toast.error('Слишком большой текст задачи или ее описание')

        let findSameTask = this.taskList.find(({title}) => title == this.task.title)

        if (findSameTask) return this.$toast.error('Такая задача уже существует')

        this.loading = true

        this.$store.dispatch('createTask', {
          ...this.task, 
          completed: false,
          priority: this.priority.list[this.priority.selectedIndex],
        })
          .then(() => {
            this.$toast.success('Задача добавлена')
            this.task.title = this.task.desc = ''
            this.priority.selectedIndex = 0
          })
          .catch(e => this.$toast.error(e))
          .finally(() => this.loading = false)

      }
    }
  }

</script>