<template lang='pug'>
  
  .tasks-list
    p(v-if='!tasksList.length').tasks-list__title Список задач пуст.
    .tasks-list__list
      .task-item(v-for='task in tasksList' :class='{"task-item_completed": task.completed}')
        .task-item__main-info
          .task-item__info
            h1.task-item__title {{task.title}}
            p.task-item__priority #[i.fas.fa-thumbtack] Приоритет задачи: #[span(:style='{"color": priorityColors[task.priority]}') {{task.priority}}]
            p.task-item__time #[i.far.fa-clock] Время добавления: #[span {{getTaskTime(task.time)}}]
          .task-item__actions
            button(v-if='!task.completed' @click='completeTask(task)').task-item__btn.task-item__btn_green #[i.fas.fa-check]
            button(@click='deleteTask(task)').task-item__btn.task-item__btn_red #[i.fas.fa-trash-alt]


</template>

<script>
  
  import moment from 'moment'
  moment.locale('ru')

  export default {
    data() {
      return {
        priorityColors: {
          "Высокий": "#f13a3a",
          "Средний": "#ec9615",
          "Низкий": "#11a753"
        }
      }
    },
    methods: {
      getTaskTime(time) {
        return moment(time).format('LLL')
      },
      completeTask(task) {
        this.$toast(`Задача "${task.title}" выполнена`)
        this.$store.dispatch('completeTask', task.key)
      },
      deleteTask(task) {
        this.$toast(`Задача "${task.title}" удалена`)
        this.$store.dispatch('deleteTask', task.key)
      } 
    },
    computed: {
      tasksList() {
        return this.$store.state.tasks.list
      }
    }
  }

</script>

<style lang="sass">

  .tasks-list
    &__title
      margin: 20px 0 0
      font-weight: 500
      font-size: 21px
      opacity: .6
    &__list
      margin: 20px 0 0
    
  .task-item
    width: 810px
    margin: 0 0 30px
    padding: 23px 25px
    background: #fff
    border: 1px solid #dcdcdc
    border-radius: 5px
    font-family: $mont
    box-shadow: 0 3px 12px rgba(#000, 0.1)
    transition: opacity .2s
    &_completed
      opacity: .5
      user-select: none
    &__main-info
      @include flexRow(space-between, center)
    &__btn
      margin: 0 0 0 15px
      width: 44px
      height: 44px
      border: 0
      border-radius: 50%
      font-size: 16px
      color: #fff
      transition: background .2s
      &_green
        background: #13bf60
        &:hover
          background: #0f9a4d
      &_red
        background: #f94141
        &:hover
          background: #c13333
      &::first-child
        margin: 0
        
    &__title
      width: 90%
      margin: 0 0 20px
      font-weight: 600
      font-size: 23px
      overflow: hidden
      white-space: nowrap
      text-overflow: ellipsis
    &__priority,
    &__time
      margin: 10px 0 0
      font-weight: 500
      color: rgba(#000,0.6)
      span
        font-weight: 600

</style>
